<template>
  <v-container id="user_t" fluid tag="section">
    <v-row justify="center">
      <v-col cols="12" md="8">
        <base-material-card>
          <template v-slot:heading>
            <div class="display-2" style="margin-bottom: 5px;">
              我的信息
            </div>

            <!-- <div class="subtitle-1 font-weight-light">
              Complete your profile
            </div> -->
          </template>

          <v-form>
            <v-container class="py-0">
              <v-row>
                <v-col cols="12" style="margin-top: 15px;">
                  账号：{{ userMsg.account }}
                </v-col>

                <v-col cols="12">
                  学校：{{ userMsg.school_id }}
                </v-col>

                <v-col cols="12">
                  教学班级：{{ userMsg.classes }}
                </v-col>

                <v-col cols="12">
                  班级编号：{{ userMsg.classes }}
                </v-col>

                <v-col cols="12">
                  <v-text-field label="请输入学校名称" v-model="create_school" type="text">
                  </v-text-field>
                  <v-btn @click="build_school" style="margin-right: 15px;">
                    创建学校
                  </v-btn>

                  <v-text-field label="请输入学校id" v-model="school_id" type="text">
                  </v-text-field>
                  <v-text-field label="请输入班级名称" v-model="create_class" type="text">
                  </v-text-field>
                  <v-text-field label="请输入教师id" v-model="math_teacher_id" type="text">
                  </v-text-field>
                  <v-btn @click="build_class" style="margin-right: 15px;">
                    创建班级
                  </v-btn>

                </v-col>
              </v-row>
            </v-container>
          </v-form>
        </base-material-card>
      </v-col>

      <v-col cols="12" md="4">
        <base-material-card class="v-card-profile" :avatar="userMsg.avatar">
          <v-card-text class="text-center">
            <h4 class="display-2 font-weight-light mb-3 black--text">
            {{ userMsg.name }}
            </h4>

            <h6 class="display-1 mb-1 grey--text" style="margin-top: 20px;">
              班级：
              <template v-for="(item, i) in userMsg.classes">{{ userMsg.classes[i] }}</template>
            </h6>

            <h6 class="display-1 mb-1 grey--text" style="margin-top: 25px;">
              学号：{{ userMsg.number }}
            </h6>
            <!-- 修改头像 -->
            <template v-if="flag3==1">
              <v-file-input id="image" :rules="rules" accept="image/*" placeholder="上传头像" chips label="头像" outlined
                dense prepend-icon="mdi-camera" style="margin-top: 30px;"></v-file-input>
            </template>
            <v-btn v-if="flag3==0" color="success" rounded class="mr-0" style="margin-top: 30px;" @click="flag3=1">
              修改头像
            </v-btn>
            <v-btn v-else color="success" rounded class="mr-0" style="margin-top: 30px;" @click="upload">
              上传
            </v-btn>
          </v-card-text>
        </base-material-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
  export default {
    name: 'user',

    data() {
      return {
        userMsg: {
          id: "",
          name: "",
          number: "",
          school_name: "",
          school_id: "",
          classes: [],
          avatar: "",
          account: "",
          password: "",
          identity: "",
        },
        flag3: 0,
        flag4: 0,
        flag5: 0,
        create_school: '',
        create_class: '',
        school_id: '',
        math_teacher_id: '',
        rules: [
          value => !value || value.size < 2000000 || '头像应该小于 2 MB！',
        ],
      }
    },
    created() {
      var that = this;
      console.log("created")
      that.userMsg.account = that.$global.userMsg.account
      that.userMsg.name = that.$global.userMsg.name
      that.userMsg.number = that.$global.userMsg.number
      that.userMsg.school_name = that.$global.userMsg.school_name
      that.userMsg.classes = that.$global.userMsg.classes
      that.userMsg.identity = that.$global.userMsg.identity
      that.userMsg.avatar = that.$global.userMsg.avatar
    },
    methods: {
      build_school: function () {
        var that = this;
        this.$axios({
          method: 'get',
          url: '/api/build_school/',
          params: {
            name: that.create_school,
          },
        })
        .then(function (response) {
          if (response.data.error_num == 0) {
            console.log(response.data)
            that.$forceUpdate();
            alert("创建学校成功！")
          } else {
            alert("创建学校失败！")
          }
        })
      },
      build_class: function () {
        var that = this;
        this.$axios({
          method: 'get',
          url: '/api/build_class/',
          params: {
            name: that.create_class,
            school_id: that.school_id,
            math_teacher_id: that.math_teacher_id,
          },
        })
        .then(function (response) {
          if (response.data.error_num == 0) {
            console.log(response.data)
            that.$forceUpdate();
            alert("创建班级成功！")
          } else {
            alert("创建班级失败！")
          }
        })
      },
    }
  }
</script>